import {creditsLogList} from "../../api/userInfo";
import rfLoadMore from '@/components/rf-load-more/rf-load-more.vue';
import empty from "@/components/empty";
import moment from '@/utils/moment';
export default {
  name: "Integral",
  components: {
    rfLoadMore,
    empty
  },
  data () {
    return {
      navList: [
          {name: "分值明细" },
          {name: "分值提升" }
      ],
      current: 0,
      integralList: [],
      loadingType: 'more',
      page: 1,
      userInfo: {},
      loading: true
    };
  },
  onLoad () {
    this.initData();
  },
  filters: {
    time(val) {
      return moment(val * 1000).format('YYYY-MM-DD HH:mm:ss')
    },
    numFilter(val) {
      return val >= 0 ? `+${val.toString()}` : val;
    }
  },
  //加载更多
  onReachBottom(){
    this.page ++;
    this.getIntegralListList();
  },
  methods: {
    toCategory () {
      uni.reLaunch({
        url: `/pages/category/category`
      })
    },
    nav (index) {
      this.loading = true;
      this.current = index;
      this.page = 1;
      this.integralList.length = 0;
      this.getIntegralListList();
    },
    initData () {
      this.token = uni.getStorageSync('accessToken') || undefined;
      this.userInfo = uni.getStorageSync('userInfo') || undefined;
      if (this.token) {
        this.getIntegralListList();
      }
    },
    async getIntegralListList () {
        const params = {};
        params.page = this.page;
        await this.$get(`${creditsLogList}`, {
          ...params
        }).then(r=>{
          this.loading = false;
          this.loadingType  = r.data.length === 10 ? 'more' : 'nomore';
          this.integralList = [ ...this.integralList, ...r.data ]
        }).catch(() => {
          this.loading = false;
        });
      },
  }
};
